import React, { useEffect, useState } from "react";
import { getAllFragrances } from "./restApi.ts"; // or './api/graphqlApi' for GraphQL

// interface Fragrance {
//   id: string;
//   name: string;
//   description: string;
//   category: string;
//   created_at: string;
//   updated_at: string;
//   image_url: string;
// }

const MyFragrances = () => {
  const [fragrances, setFragrances] = useState([]);

  useEffect(() => {
    // console.log("data");

    const fetchFragrances = async () => {
      console.log("data");

      const data = await getAllFragrances();
      console.log("data", data);
      setFragrances(data);
    };

    fetchFragrances();
  }, []);

  return (
    <div className="Fragrance">
      <h1>Fragrances</h1>
      <ul>
        {fragrances.map((fragrance) => (
          <li key={fragrance.id}>
            <h2>{fragrance.name}</h2>
            <p>{fragrance.description}</p>
            <img src={fragrance.image_url} alt={fragrance.name} />
          </li>
        ))}
      </ul>
    </div>
  );
};

export default MyFragrances;
